import { useState } from "react";
import { useQuery } from "@tanstack/react-query";
import { DialogHeader, DialogTitle, DialogDescription } from "@/components/ui/dialog";
import { Button } from "@/components/ui/button";
import { Badge } from "@/components/ui/badge";
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from "@/components/ui/card";
import { Table, TableBody, TableCell, TableHead, TableHeader, TableRow } from "@/components/ui/table";
import { Tabs, TabsContent, TabsList, TabsTrigger } from "@/components/ui/tabs";
import { Loader2, Phone, Users, Clock, CheckCircle, BarChart3, Download, Play, Eye } from "lucide-react";
import { toast } from "sonner";

import {
  getCampaignById,
  getCampaignCalls,
  type Campaign,
  type CampaignCall
} from "@/api/services/outbound/campaignService";
import { getPhoneNumberById } from "@/api/services/phoneNumbers/phoneNumberService";
import { getAssistantById } from "@/api/services/agents/agentService";
import { getCampaignCallRecording, getCampaignCallTranscript, formatDuration } from "@/api/services/callHistory/callHistoryService";
import { CallRecordingPlayer } from "@/components/CallRecordingPlayer";

interface CampaignDetailsModalProps {
  campaignId: string;
  onClose: () => void;
}

export function CampaignDetailsModal({ campaignId, onClose }: CampaignDetailsModalProps) {
  const [activeTab, setActiveTab] = useState("overview");
  const [selectedCallForRecording, setSelectedCallForRecording] = useState<CampaignCall | null>(null);
  const [showRecordingPlayer, setShowRecordingPlayer] = useState(false);

  // Fetch campaign details
  const { data: campaignData, isLoading: campaignLoading } = useQuery({
    queryKey: ["campaign", campaignId],
    queryFn: () => getCampaignById(campaignId),
    retry: 1,
  });

  // Fetch campaign calls
  const { data: callsData, isLoading: callsLoading } = useQuery({
    queryKey: ["campaignCalls", campaignId],
    queryFn: () => getCampaignCalls(campaignId),
    retry: 1,
  });

  const campaign: Campaign = campaignData?.data;
  const calls: Record<string, CampaignCall> = callsData?.data || {};
  const callsList = Object.values(calls);

  // Debug: Log the actual call data structure
  console.log('ðŸ” Campaign calls data:', calls);
  console.log('ðŸ” Calls list:', callsList);
  if (callsList.length > 0) {
    console.log('ðŸ” First call structure:', callsList[0]);
    console.log('ðŸ” First call customer:', callsList[0].customer);
    console.log('ðŸ” First call status:', callsList[0].status);
  }

  // Helper function to format call data for display
  const formatCallForDisplay = (call: CampaignCall) => {
    // Calculate duration from timestamps if not provided
    let duration = call.duration || 0;
    if (!duration && call.startedAt && call.endedAt) {
      const start = new Date(call.startedAt).getTime();
      const end = new Date(call.endedAt).getTime();
      duration = Math.round((end - start) / 1000); // duration in seconds
    }

    return {
      ...call,
      duration,
      // Ensure customer data is accessible
      customer: call.customer || { name: null, email: null, number: null }
    };
  };

  // Debug the calls data structure
  console.log('Raw calls data:', callsData?.data);
  console.log('Calls object:', calls);
  console.log('Calls list:', callsList);
  console.log('First call in list:', callsList[0]);
  if (callsList[0]) {
    console.log('First call ID access:', callsList[0].id);
    console.log('First call keys:', Object.keys(callsList[0]));
  }

  // Fetch phone number details
  const { data: phoneNumberData } = useQuery({
    queryKey: ["phoneNumber", campaign?.phoneNumberId],
    queryFn: () => getPhoneNumberById(campaign.phoneNumberId),
    enabled: !!campaign?.phoneNumberId,
    retry: 1,
  });

  // Fetch assistant details
  const { data: assistantData } = useQuery({
    queryKey: ["assistant", campaign?.assistantId],
    queryFn: () => getAssistantById(campaign.assistantId!),
    enabled: !!campaign?.assistantId,
    retry: 1,
  });

  // Convert CampaignCall to CallHistoryItem for recording player
  const convertToCallHistoryItem = (call: CampaignCall) => {
    console.log('Converting campaign call to CallHistoryItem:', call);

    // Since the backend now properly parses the JSON strings, we can access properties directly
    const callId = call.id;

    const converted = {
      id: callId,
      call_id: callId, // Use the same ID for call_id
      customer_number: call.customer?.number || '',
      status: call.status as any,
      type: 'outbound' as const,
      duration: call.duration || 0,
      cost: call.cost || 0,
      transcript: call.transcript || call.analysis?.summary || '',
      recording_url: call.recording,
      recordingUrl: call.recording,
      recording: call.recording,
      assistant_id: campaign?.assistantId,
      assistant_name: assistantData?.data?.name,
      end_reason: call.endedReason,
      started_at: call.startedAt,
      ended_at: call.endedAt,
      created_at: call.startedAt || new Date().toISOString(),
      updated_at: call.endedAt || new Date().toISOString(),
      metadata: { customer: call.customer }
    };

    console.log('Converted CallHistoryItem:', converted);
    return converted;
  };

  const getStatusBadge = (status: string) => {
    const variants: Record<string, { variant: "default" | "secondary" | "destructive" | "outline", icon: React.ReactNode }> = {
      scheduled: { variant: "outline", icon: <Clock className="h-3 w-3 mr-1" /> },
      "in-progress": { variant: "default", icon: <Play className="h-3 w-3 mr-1" /> },
      ended: { variant: "secondary", icon: <CheckCircle className="h-3 w-3 mr-1" /> },
    };
    
    const config = variants[status] || { variant: "outline", icon: null };
    
    return (
      <Badge variant={config.variant} className="flex items-center">
        {config.icon}
        {status === "in-progress" ? "Running" : status.charAt(0).toUpperCase() + status.slice(1)}
      </Badge>
    );
  };

  const getCallStatusBadge = (status: string) => {
    const variants: Record<string, "default" | "secondary" | "destructive" | "outline"> = {
      queued: "outline",
      ringing: "secondary",
      "in-progress": "default",
      ended: "secondary",
      failed: "destructive",
    };
    return <Badge variant={variants[status] || "outline"}>{status}</Badge>;
  };

  const formatDuration = (seconds?: number) => {
    if (!seconds) return "0:00";
    const mins = Math.floor(seconds / 60);
    const secs = seconds % 60;
    return `${mins}:${secs.toString().padStart(2, '0')}`;
  };

  if (campaignLoading) {
    return (
      <>
        <DialogHeader>
          <DialogTitle>Campaign Details</DialogTitle>
        </DialogHeader>
        <div className="flex items-center justify-center h-32">
          <Loader2 className="h-8 w-8 animate-spin" />
        </div>
      </>
    );
  }

  if (!campaign) {
    return (
      <>
        <DialogHeader>
          <DialogTitle>Campaign Details</DialogTitle>
        </DialogHeader>
        <div className="text-center py-8">
          <p className="text-muted-foreground">Campaign not found</p>
        </div>
      </>
    );
  }

  return (
    <>
      <DialogHeader>
        <DialogTitle className="flex items-center gap-2">
          {campaign.name}
          {getStatusBadge(campaign.status)}
        </DialogTitle>
        <DialogDescription>
          Created on {new Date(campaign.createdAt).toLocaleDateString()}
        </DialogDescription>
      </DialogHeader>

      <Tabs value={activeTab} onValueChange={setActiveTab} className="w-full">
        <TabsList className="grid w-full grid-cols-3">
          <TabsTrigger value="overview">Overview</TabsTrigger>
          <TabsTrigger value="calls">Calls</TabsTrigger>
          <TabsTrigger value="analytics">Analytics</TabsTrigger>
        </TabsList>

        <TabsContent value="overview" className="space-y-4">
          <div className="grid grid-cols-2 md:grid-cols-4 gap-4">
            <Card>
              <CardHeader className="pb-2">
                <CardTitle className="text-sm font-medium flex items-center gap-2">
                  <Users className="h-4 w-4" />
                  Total Calls
                </CardTitle>
              </CardHeader>
              <CardContent>
                <div className="text-2xl font-bold">
                  {callsList.length}
                </div>
                <p className="text-xs text-muted-foreground mt-1">
                  Actual calls made
                </p>
              </CardContent>
            </Card>

            <Card>
              <CardHeader className="pb-2">
                <CardTitle className="text-sm font-medium flex items-center gap-2">
                  <CheckCircle className="h-4 w-4" />
                  Completed
                </CardTitle>
              </CardHeader>
              <CardContent>
                <div className="text-2xl font-bold">{campaign.callsCounterEnded || 0}</div>
              </CardContent>
            </Card>

            <Card>
              <CardHeader className="pb-2">
                <CardTitle className="text-sm font-medium flex items-center gap-2">
                  <BarChart3 className="h-4 w-4" />
                  Pick Up
                </CardTitle>
              </CardHeader>
              <CardContent>
                <div className="text-2xl font-bold">
                  {campaign.callsCounterEnded > 0 
                    ? `${Math.round(((campaign.callsCounterEnded - campaign.callsCounterEndedVoicemail) / campaign.callsCounterEnded) * 100)}%`
                    : "0%"
                  }
                </div>
              </CardContent>
            </Card>

            <Card>
              <CardHeader className="pb-2">
                <CardTitle className="text-sm font-medium flex items-center gap-2">
                  <Phone className="h-4 w-4" />
                  Voicemail
                </CardTitle>
              </CardHeader>
              <CardContent>
                <div className="text-2xl font-bold">{campaign.callsCounterEndedVoicemail || 0}</div>
              </CardContent>
            </Card>
          </div>

          <Card>
            <CardHeader>
              <CardTitle>Campaign Information</CardTitle>
            </CardHeader>
            <CardContent className="space-y-3">
              <div className="grid grid-cols-2 gap-4">
                <div>
                  <p className="text-sm font-medium text-muted-foreground">Phone Number</p>
                  <p className="text-sm font-mono">
                    {phoneNumberData?.data?.number || phoneNumberData?.data?.friendlyName || campaign.phoneNumberId}
                  </p>
                  {phoneNumberData?.data?.number && (
                    <p className="text-xs text-muted-foreground">
                      {phoneNumberData.data.friendlyName || 'AI CRUITMENT Number'}
                    </p>
                  )}
                </div>
                <div>
                  <p className="text-sm font-medium text-muted-foreground">Assistant</p>
                  <p className="text-sm">
                    {assistantData?.data?.name || campaign.assistantId || campaign.workflowId || "N/A"}
                  </p>
                  {assistantData?.data?.name && (
                    <p className="text-xs text-muted-foreground">
                      ID: {campaign.assistantId}
                    </p>
                  )}
                </div>
                <div>
                  <p className="text-sm font-medium text-muted-foreground">Created</p>
                  <p className="text-sm">{new Date(campaign.createdAt).toLocaleString()}</p>
                </div>
                <div>
                  <p className="text-sm font-medium text-muted-foreground">Last Updated</p>
                  <p className="text-sm">{new Date(campaign.updatedAt).toLocaleString()}</p>
                </div>
              </div>
            </CardContent>
          </Card>
        </TabsContent>

        <TabsContent value="calls" className="space-y-4">
          <Card>
            <CardHeader>
              <CardTitle className="flex items-center gap-2">
                <Phone className="h-5 w-5" />
                Campaign Calls
              </CardTitle>
              <CardDescription>
                Individual call details and outcomes
              </CardDescription>
            </CardHeader>
            <CardContent>
              {callsLoading ? (
                <div className="flex items-center justify-center h-32">
                  <Loader2 className="h-8 w-8 animate-spin" />
                </div>
              ) : (
                <Table>
                  <TableHeader>
                    <TableRow>
                      <TableHead>Customer</TableHead>
                      <TableHead>Phone Number</TableHead>
                      <TableHead>Status</TableHead>
                      <TableHead>Duration</TableHead>
                      <TableHead>Started</TableHead>
                      <TableHead>Recording</TableHead>
                    </TableRow>
                  </TableHeader>
                  <TableBody>
                    {callsList.length === 0 ? (
                      <TableRow>
                        <TableCell colSpan={6} className="text-center text-muted-foreground py-8">
                          No calls found for this campaign
                        </TableCell>
                      </TableRow>
                    ) : (
                      callsList.map((call) => {
                        const formattedCall = formatCallForDisplay(call);
                        return (
                        <TableRow key={call.id}>
                          <TableCell>
                            <div>
                              <div className="font-medium">{formattedCall.customer?.name || "Unknown Customer"}</div>
                              <div className="text-xs text-muted-foreground">{formattedCall.customer?.email || "-"}</div>
                            </div>
                          </TableCell>
                          <TableCell className="font-mono">{formattedCall.customer?.number || "-"}</TableCell>
                          <TableCell>{getCallStatusBadge(formattedCall.status)}</TableCell>
                          <TableCell>{formatDuration(formattedCall.duration)}</TableCell>
                          <TableCell>
                            {formattedCall.startedAt ? new Date(formattedCall.startedAt).toLocaleString() : "-"}
                          </TableCell>
                          <TableCell>
                            <div className="flex gap-2">
                              <Button
                                size="sm"
                                variant="outline"
                                onClick={() => {
                                  setSelectedCallForRecording(call);
                                  setShowRecordingPlayer(true);
                                }}
                                title="Play Recording & View Transcript"
                              >
                                <Play className="h-3 w-3 mr-1" />
                                Recording
                              </Button>
                            </div>
                          </TableCell>
                        </TableRow>
                        );
                      })
                    )}
                  </TableBody>
                </Table>
              )}
            </CardContent>
          </Card>
        </TabsContent>

        <TabsContent value="analytics" className="space-y-4">
          <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
            <Card>
              <CardHeader>
                <CardTitle>Call Status Distribution</CardTitle>
              </CardHeader>
              <CardContent>
                <div className="space-y-2">
                  <div className="flex justify-between">
                    <span className="text-sm">Scheduled</span>
                    <span className="text-sm font-medium">{campaign.callsCounterScheduled || 0}</span>
                  </div>
                  <div className="flex justify-between">
                    <span className="text-sm">Queued</span>
                    <span className="text-sm font-medium">{campaign.callsCounterQueued || 0}</span>
                  </div>
                  <div className="flex justify-between">
                    <span className="text-sm">In Progress</span>
                    <span className="text-sm font-medium">{campaign.callsCounterInProgress || 0}</span>
                  </div>
                  <div className="flex justify-between">
                    <span className="text-sm">Completed</span>
                    <span className="text-sm font-medium">{campaign.callsCounterEnded || 0}</span>
                  </div>
                  <div className="flex justify-between">
                    <span className="text-sm">Voicemail</span>
                    <span className="text-sm font-medium">{campaign.callsCounterEndedVoicemail || 0}</span>
                  </div>
                </div>
              </CardContent>
            </Card>

            <Card>
              <CardHeader>
                <CardTitle>Performance Metrics</CardTitle>
              </CardHeader>
              <CardContent>
                <div className="space-y-2">
                  <div className="flex justify-between">
                    <span className="text-sm">Success Rate</span>
                    <span className="text-sm font-medium">
                      {campaign.callsCounterEnded > 0 
                        ? `${Math.round(((campaign.callsCounterEnded - campaign.callsCounterEndedVoicemail) / campaign.callsCounterEnded) * 100)}%`
                        : "0%"
                      }
                    </span>
                  </div>
                  <div className="flex justify-between">
                    <span className="text-sm">Completion Rate</span>
                    <span className="text-sm font-medium">
                      {campaign.customers?.length > 0 
                        ? `${Math.round((campaign.callsCounterEnded / campaign.customers.length) * 100)}%`
                        : "0%"
                      }
                    </span>
                  </div>
                  <div className="flex justify-between">
                    <span className="text-sm">Avg Duration</span>
                    <span className="text-sm font-medium">
                      {callsList.length > 0
                        ? formatDuration(Math.round(callsList.reduce((sum, call) => sum + (call.duration || 0), 0) / callsList.length))
                        : "0:00"
                      }
                    </span>
                  </div>
                  <div className="flex justify-between">
                    <span className="text-sm">Total Cost</span>
                    <span className="text-sm font-medium">
                      ${callsList.reduce((sum, call) => sum + (call.cost || 0), 0).toFixed(2)}
                    </span>
                  </div>
                </div>
              </CardContent>
            </Card>

            <Card className="md:col-span-2">
              <CardHeader>
                <CardTitle>Call Outcomes</CardTitle>
              </CardHeader>
              <CardContent>
                <div className="grid grid-cols-2 md:grid-cols-4 gap-4">
                  <div className="text-center">
                    <div className="text-2xl font-bold text-green-600">
                      {callsList.filter(call => call.status === 'ended' && call.duration && call.duration > 30).length}
                    </div>
                    <div className="text-xs text-muted-foreground">Successful Calls</div>
                  </div>
                  <div className="text-center">
                    <div className="text-2xl font-bold text-yellow-600">
                      {callsList.filter(call => call.status === 'ended' && (!call.duration || call.duration <= 30)).length}
                    </div>
                    <div className="text-xs text-muted-foreground">Short Calls</div>
                  </div>
                  <div className="text-center">
                    <div className="text-2xl font-bold text-orange-600">
                      {campaign.callsCounterEndedVoicemail || 0}
                    </div>
                    <div className="text-xs text-muted-foreground">Voicemails</div>
                  </div>
                  <div className="text-center">
                    <div className="text-2xl font-bold text-red-600">
                      {callsList.filter(call => call.status !== 'ended').length}
                    </div>
                    <div className="text-xs text-muted-foreground">Failed Calls</div>
                  </div>
                </div>
              </CardContent>
            </Card>
          </div>
        </TabsContent>
      </Tabs>

      <div className="flex justify-end pt-4 border-t">
        <Button onClick={onClose}>Close</Button>
      </div>

      {/* Call Recording Player Modal */}
      {showRecordingPlayer && selectedCallForRecording && (
        <CallRecordingPlayer
          call={convertToCallHistoryItem(selectedCallForRecording)}
          isOpen={showRecordingPlayer}
          onClose={() => {
            setShowRecordingPlayer(false);
            setSelectedCallForRecording(null);
          }}
          customGetRecording={getCampaignCallRecording}
          customGetTranscript={getCampaignCallTranscript}
        />
      )}
    </>
  );
}
