import { useState } from "react";
import { useQuery, useMutation, useQueryClient } from "@tanstack/react-query";
import { useNavigate } from "react-router-dom";
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from "@/components/ui/card";
import { Button } from "@/components/ui/button";
import { Badge } from "@/components/ui/badge";
import { Table, TableBody, TableCell, TableHead, TableHeader, TableRow } from "@/components/ui/table";
import { Dialog, DialogContent, DialogHeader, DialogTitle } from "@/components/ui/dialog";
import { Loader2, Plus, Play, Pause, Square, BarChart3, Phone, Users, Clock, CheckCircle, AlertCircle, Trash2, Eye, RefreshCw } from "lucide-react";
import { toast } from "sonner";
import Page from "@/components/Page";
import { CampaignDetailsModal } from "./CampaignDetailsModal";
import axiosInstance from "@/api/axios/axiosInstance";
import { useAuth } from "@/context/AuthContext";
import { usePermissions } from "@/context/PermissionContext";

import {
  getAllCampaigns,
  launchCampaign,
  pauseCampaign,
  resumeCampaign,
  cancelCampaign,
  deleteCampaign,
  testVapiConnection,
  type Campaign
} from "@/api/services/outbound/campaignService";

const Outbound = () => {
  const navigate = useNavigate();
  const [isDetailsModalOpen, setIsDetailsModalOpen] = useState(false);
  const [selectedCampaign, setSelectedCampaign] = useState<Campaign | null>(null);
  const [showInsufficientCreditsModal, setShowInsufficientCreditsModal] = useState(false);
  const [currentBalance, setCurrentBalance] = useState(0);

  const queryClient = useQueryClient();
  const { user } = useAuth();
  const { isSuperAdmin } = usePermissions();

  // Fetch campaigns
  const { data: campaignsData, isLoading: campaignsLoading, error: campaignsError } = useQuery({
    queryKey: ["outboundCampaigns"],
    queryFn: () => getAllCampaigns(),
    retry: (failureCount, error: any) => {
      // Don't retry for expected scenarios (normal for new users)
      const expectedStatuses = [404, 204, 402]; // 402 = Payment Required (no credits)
      if (expectedStatuses.includes(error?.response?.status)) {
        return false;
      }
      return failureCount < 1; // Retry once for other errors
    },
  });

  // Launch campaign mutation
  const launchMutation = useMutation({
    mutationFn: launchCampaign,
    onSuccess: () => {
      queryClient.invalidateQueries({ queryKey: ["outboundCampaigns"] });
      toast.success("Campaign launched successfully");
    },
    onError: (error: any) => {
      const message = error.response?.data?.message || "Failed to launch campaign";
      const currentStatus = error.response?.data?.currentStatus;

      if (currentStatus === "in-progress") {
        toast.info("Campaign is already running");
      } else {
        toast.error(message);
      }

      // Refresh data to show current status
      queryClient.invalidateQueries({ queryKey: ["outboundCampaigns"] });
    },
  });

  // Pause campaign mutation
  const pauseMutation = useMutation({
    mutationFn: pauseCampaign,
    onSuccess: () => {
      queryClient.invalidateQueries({ queryKey: ["outboundCampaigns"] });
    },
  });

  // Resume campaign mutation
  const resumeMutation = useMutation({
    mutationFn: resumeCampaign,
    onSuccess: () => {
      queryClient.invalidateQueries({ queryKey: ["outboundCampaigns"] });
    },
  });

  // Cancel campaign mutation
  const cancelMutation = useMutation({
    mutationFn: cancelCampaign,
    onSuccess: () => {
      queryClient.invalidateQueries({ queryKey: ["outboundCampaigns"] });
    },
  });

  // Delete campaign mutation
  const deleteMutation = useMutation({
    mutationFn: deleteCampaign,
    onSuccess: () => {
      queryClient.invalidateQueries({ queryKey: ["outboundCampaigns"] });
    },
  });

  const handleLaunchCampaign = (campaignId: string) => {
    launchMutation.mutate(campaignId);
  };

  const handlePauseCampaign = (campaignId: string) => {
    pauseMutation.mutate(campaignId);
  };

  const handleResumeCampaign = (campaignId: string) => {
    resumeMutation.mutate(campaignId);
  };

  const handleCancelCampaign = (campaignId: string) => {
    if (confirm("Are you sure you want to cancel this campaign?")) {
      cancelMutation.mutate(campaignId);
    }
  };

  const handleDeleteCampaign = (campaignId: string) => {
    if (confirm("Are you sure you want to delete this campaign? This action cannot be undone.")) {
      deleteMutation.mutate(campaignId);
    }
  };

  const handleViewCampaign = (campaign: Campaign) => {
    setSelectedCampaign(campaign);
    setIsDetailsModalOpen(true);
  };

  const handleTestVapi = async (campaignId?: string) => {
    try {
      const result = await testVapiConnection(campaignId);
      console.log("VAPI Test Result:", result);
      toast.success("VAPI connection test successful");
    } catch (error) {
      console.error("VAPI Test Error:", error);
      toast.error("VAPI connection test failed");
    }
  };

  const handleRefresh = () => {
    queryClient.invalidateQueries({ queryKey: ["outboundCampaigns"] });
    toast.success("Campaigns refreshed");
  };

  const handleCreateCampaign = async () => {
    // Super admins have unlimited access
    if (isSuperAdmin) {
      navigate("/outbound/create-campaign");
      return;
    }

    try {
      // Check credit balance for regular users
      const response = await axiosInstance.get('/api/credits/balance');
      const balance = response.data.data;

      if (!balance || parseFloat(balance.available_credits) < 1) {
        // Store current balance and show insufficient credits modal
        setCurrentBalance(balance?.available_credits || 0);
        setShowInsufficientCreditsModal(true);
        return;
      }

      // User has sufficient credits, proceed to create campaign page
      navigate("/outbound/create-campaign");
    } catch (error) {
      console.error('Error checking credit balance:', error);
      toast.error('Unable to verify credit balance. Please try again.');
    }
  };

  const getStatusBadge = (status: string) => {
    const variants: Record<string, { variant: "default" | "secondary" | "destructive" | "outline", icon: React.ReactNode, label: string }> = {
      scheduled: { variant: "outline", icon: <Clock className="h-3 w-3 mr-1" />, label: "Scheduled" },
      "in-progress": { variant: "default", icon: <Play className="h-3 w-3 mr-1" />, label: "Running" },
      ended: { variant: "secondary", icon: <CheckCircle className="h-3 w-3 mr-1" />, label: "Ended" },
    };

    const config = variants[status] || {
      variant: "outline",
      icon: <AlertCircle className="h-3 w-3 mr-1" />,
      label: status.charAt(0).toUpperCase() + status.slice(1)
    };

    return (
      <Badge variant={config.variant} className="flex items-center">
        {config.icon}
        {config.label}
      </Badge>
    );
  };

  const campaigns = Array.isArray(campaignsData?.data) ? campaignsData.data : [];

  if (campaignsError) {
    const errorStatus = (campaignsError as any)?.response?.status;
    const isInsufficientCredits = errorStatus === 402;

    return (
      <Page>
        <div className="space-y-6">
          <div>
            <h1 className="text-3xl font-bold tracking-tight">Outbound</h1>
            <p className="text-muted-foreground">
              Create and manage outbound campaigns
            </p>
          </div>
          <Card>
            <CardContent className="pt-6">
              <div className="text-center">
                {isInsufficientCredits ? (
                  <>
                    <AlertCircle className="h-12 w-12 text-orange-500 mx-auto mb-4" />
                    <h3 className="text-lg font-semibold mb-2">Insufficient Credits</h3>
                    <p className="text-muted-foreground mb-4">
                      You need credits to access campaign features. Please purchase credits to continue.
                    </p>
                    <div className="flex gap-2 justify-center">
                      <Button onClick={() => navigate('/credits/purchase')} className="bg-orange-500 hover:bg-orange-600">
                        Purchase Credits
                      </Button>
                      <Button variant="outline" onClick={() => window.location.reload()}>
                        <RefreshCw className="h-4 w-4 mr-2" />
                        Refresh
                      </Button>
                    </div>
                  </>
                ) : (
                  <>
                    <p className="text-muted-foreground mb-4">
                      Unable to connect to the backend API. Please ensure the backend server is running.
                    </p>
                    <Button onClick={() => window.location.reload()}>
                      Retry Connection
                    </Button>
                  </>
                )}
              </div>
            </CardContent>
          </Card>
        </div>
      </Page>
    );
  }

  return (
    <Page>
      <div className="space-y-6">
        {/* Header */}
        <div className="flex items-center justify-between">
          <div>
            <h1 className="text-3xl font-bold tracking-tight">Outbound</h1>
            <p className="text-muted-foreground">
              Create and manage outbound campaigns
            </p>
          </div>
          <div className="flex gap-2">
            <Button
              variant="outline"
              onClick={handleRefresh}
              title="Refresh Campaigns"
            >
              <RefreshCw className="h-4 w-4 mr-2" />
              Refresh
            </Button>
           
            <Button
              className="bg-teal-600 hover:bg-teal-700 text-white"
              onClick={handleCreateCampaign}
            >
              <Plus className="h-4 w-4 mr-2" />
              Create Campaign
            </Button>
          </div>
        </div>

        {/* Campaigns Section */}
        <Card>
          <CardHeader>
            <CardTitle className="flex items-center gap-2">
              <Phone className="h-5 w-5" />
              Campaigns
            </CardTitle>
            <CardDescription>
              Manage your outbound call campaigns
            </CardDescription>
          </CardHeader>
          <CardContent>
            {campaignsLoading ? (
              <div className="flex items-center justify-center h-32">
                <Loader2 className="h-8 w-8 animate-spin" />
              </div>
            ) : (
              <Table>
                <TableHeader>
                  <TableRow>
                    <TableHead>Campaign Name</TableHead>
                    <TableHead>Status</TableHead>
                    <TableHead>Total Calls</TableHead>
                    <TableHead>Completed</TableHead>
                    <TableHead>Pick Up</TableHead>
                    {/* <TableHead>Voicemail</TableHead> */}
                    <TableHead>Actions</TableHead>
                  </TableRow>
                </TableHeader>
                <TableBody>
                  {campaigns.length === 0 ? (
                    <TableRow>
                      <TableCell colSpan={7} className="text-center text-muted-foreground py-8">
                        No campaigns found. Create your first campaign to get started.
                      </TableCell>
                    </TableRow>
                  ) : (
                    campaigns.map((campaign: Campaign) => (
                      <TableRow key={campaign.id}>
                        <TableCell>
                          <div>
                            <div className="font-medium">{campaign.name}</div>
                            <div className="text-xs text-muted-foreground">
                              Created on {new Date(campaign.createdAt).toLocaleDateString()}
                            </div>
                          </div>
                        </TableCell>
                        <TableCell>{getStatusBadge(campaign.status)}</TableCell>
                        <TableCell>
                          <div className="flex items-center gap-1">
                            <Users className="h-3 w-3" />
                            {/* Show actual calls count - use customers length or ended calls */}
                            {campaign.customers?.length || campaign.callsCounterEnded || 0}
                            {campaign.status === "in-progress" && (
                              <span className="ml-2 text-xs text-green-600 font-medium">
                                ({campaign.callsCounterInProgress || 0} active)
                              </span>
                            )}
                          </div>
                        </TableCell>
                        <TableCell>
                          <div className="flex items-center gap-1">
                            <CheckCircle className="h-3 w-3" />
                            {campaign.callsCounterEnded || 0}
                          </div>
                        </TableCell>
                        <TableCell>
                          <div className="flex items-center gap-1">
                            <BarChart3 className="h-3 w-3" />
                            {campaign.callsCounterEnded > 0 
                              ? `${Math.round(((campaign.callsCounterEnded - campaign.callsCounterEndedVoicemail) / campaign.callsCounterEnded) * 100)}%`
                              : "0%"
                            }
                          </div>
                        </TableCell>
                        {/* <TableCell>
                          <div className="flex items-center gap-1">
                            <Phone className="h-3 w-3" />
                            {campaign.callsCounterEndedVoicemail || 0}
                          </div>
                        </TableCell> */}
                        <TableCell>
                          <div className="flex gap-2">
                            {campaign.status === "scheduled" && (
                              <Button
                                size="sm"
                                variant="outline"
                                onClick={() => handleLaunchCampaign(campaign.id)}
                                disabled={launchMutation.isPending}
                                title="Launch Campaign"
                              >
                                {launchMutation.isPending ? <Loader2 className="h-3 w-3 animate-spin" /> : <Play className="h-3 w-3" />}
                              </Button>
                            )}
                            {campaign.status === "in-progress" && (
                              <Button
                                size="sm"
                                variant="outline"
                                onClick={() => handlePauseCampaign(campaign.id)}
                                disabled={pauseMutation.isPending}
                                title="Pause Campaign"
                              >
                                {pauseMutation.isPending ? <Loader2 className="h-3 w-3 animate-spin" /> : <Pause className="h-3 w-3" />}
                              </Button>
                            )}
                            {(campaign.status === "scheduled" || campaign.status === "in-progress") && (
                              <Button
                                size="sm"
                                variant="outline"
                                onClick={() => handleCancelCampaign(campaign.id)}
                                disabled={cancelMutation.isPending}
                                title="Cancel Campaign"
                              >
                                {cancelMutation.isPending ? <Loader2 className="h-3 w-3 animate-spin" /> : <Square className="h-3 w-3" />}
                              </Button>
                            )}
                            
                            <Button
                              size="sm"
                              variant="outline"
                              onClick={() => handleViewCampaign(campaign)}
                              title="View Details"
                            >
                              <Eye className="h-3 w-3" />
                            </Button>
                            {campaign.status === "ended" && (
                              <Button
                                size="sm"
                                variant="outline"
                                onClick={() => handleDeleteCampaign(campaign.id)}
                                disabled={deleteMutation.isPending}
                                title="Delete Campaign"
                              >
                                {deleteMutation.isPending ? <Loader2 className="h-3 w-3 animate-spin" /> : <Trash2 className="h-3 w-3" />}
                              </Button>
                            )}
                          </div>
                        </TableCell>
                      </TableRow>
                    ))
                  )}
                </TableBody>
              </Table>
            )}
          </CardContent>
        </Card>

        {/* Campaign Details Modal */}
        {selectedCampaign && (
          <Dialog open={isDetailsModalOpen} onOpenChange={setIsDetailsModalOpen}>
            <DialogContent className="max-w-4xl max-h-[90vh] overflow-y-auto">
              <CampaignDetailsModal
                campaignId={selectedCampaign.id}
                onClose={() => setIsDetailsModalOpen(false)}
              />
            </DialogContent>
          </Dialog>
        )}

        {/* Insufficient Credits Modal */}
        <Dialog open={showInsufficientCreditsModal} onOpenChange={setShowInsufficientCreditsModal}>
          <DialogContent className="max-w-md">
            <div className="text-center p-6">
              <div className="flex justify-center mb-4">
                <div className="p-3 bg-orange-100 rounded-full">
                  <AlertCircle className="h-8 w-8 text-orange-600" />
                </div>
              </div>

              <h3 className="text-lg font-semibold text-gray-900 mb-2">
                Insufficient Credits
              </h3>

              <p className="text-gray-600 mb-6">
                You need at least 1 credit to use campaign creation.
                <span className="block mt-1">
                  Current balance: {currentBalance} credits
                </span>
              </p>

              <div className="space-y-3">
                {/* <Button
                  onClick={() => {
                    setShowInsufficientCreditsModal(false);
                    navigate('/credits/purchase');
                  }}
                  className="w-full bg-blue-600 hover:bg-blue-700 text-white"
                >
                  <RefreshCw className="h-4 w-4 mr-2" />
                  Purchase Credits
                </Button> */}

                {/* <Button
                  variant="outline"
                  onClick={() => setShowInsufficientCreditsModal(false)}
                  className="w-full"
                >
                  Cancel
                </Button> */}

                <p className="text-xs text-gray-500">
                  Credits are added instantly after purchase
                </p>
              </div>
            </div>
          </DialogContent>
        </Dialog>
      </div>
    </Page>
  );
};

export default Outbound;
